.Toolbar {
    height: 70px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(42, 119, 170);
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}
.Logo {
    height: 50px;
}

@media (min-width: 800px) {
    .Toolbar {
        display: none;
    }
}